// src/scripts/scheduler/parseBoxData.ts
import algosdk2 from "algosdk";
var parseBoxData = (input) => {
  if (input.length !== 24) {
    throw new Error(`Box has wrong length: ${input.length} ${Buffer.from(input).toString("hex")}`);
  }
  const ret = {
    funds: Number(algosdk2.decodeUint64(input.subarray(0, 8), "safe")),
    app: Number(algosdk2.decodeUint64(input.subarray(8, 16), "safe")),
    fee: Number(algosdk2.decodeUint64(input.subarray(16, 24), "safe"))
  };
  return ret;
};
var parseBoxData_default = parseBoxData;

